import { FaceLandmarker, FilesetResolver } from "@mediapipe/tasks-vision";

const faceScan = (() => {
  let calibrationTime = 10000;
  let scanTime = 60000;
  let liteModeInterval = 3;
  let drawType = "circle";
  let drawColor = "#fff";
  let strictMode = false;
  let isScanning = false;
  let frameID = 0;
  let start_time = 0;
  let noDetectionCount = 0;
  let eyeClosedTime = -1;
  let eyeOpenedTime = -1;
  let calibrationFrameCount = 0;
  let calibrationFPS = 0;
  let detectionResult = {};
  let faceLandmarkDetector;
  let wakeLock = undefined;
  let video;
  let canvas;
  let ctx;
  let raw_intensity = [];
  let ppg_time = [];
  let onFrameCallback = ({ timeElapsed = 0, type = "", percentage = 0, isFaceInView = false, isLiteMode = false }) => {};
  let onScanFinishCallback = ({ raw_intensity = [], ppg_time = [], average_fps = 0 }) => {};
  let onErrorCallback = (err = new Error("Facescan Error.")) => {};

  const setupCamera = () =>
    new Promise(async (resolve, reject) => {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          audio: false,
          video: { facingMode: "user", aspectRatio: 16 / 9 },
        });
        video.srcObject = stream;
        video.onloadedmetadata = () => {
          resolve();
        };
      } catch (err) {
        reject(new Error("We are not able to access the Camera. Please try again.", { cause: err }));
      }
    });

  const stopScan = (noCallback = false) => {
    cancelAnimationFrame(frameID);
    faceLandmarkDetector?.close?.();
    video?.srcObject?.getTracks?.()?.forEach?.((track) => {
      track?.stop?.();
    });
    isScanning = false;
    window.onblur = undefined;
    wakeLock
      ?.release()
      .then(() => {
        console.log("WakeLock Released.");
      })
      .catch((err) => {
        console.log("WakeLock Error.");
        console.error(err);
      });
    if (!noCallback)
      onScanFinishCallback({
        raw_intensity,
        ppg_time,
        average_fps: Math.round((raw_intensity.length * 1000) / scanTime),
      });
  };

  const createPath = (points) => {
    const region = new Path2D();
    if (points?.length > 1) {
      const firstPoint = points.shift();
      region.moveTo(firstPoint[0], firstPoint[1]);
      points.forEach((point) => region.lineTo(point[0], point[1]));
      region.closePath();
    }
    return region;
  };

  const getRegion = (timestamp = 0) => {
    const imageWidth = video.videoWidth;
    const imageHeight = video.videoHeight;
    const result = faceLandmarkDetector.detectForVideo(video, timestamp);
    const faceLandmarks = result?.faceLandmarks?.[0] ?? [];
    const faceBlendshapes = Object.fromEntries(
      result?.faceBlendshapes?.[0]?.categories?.map?.((shape) => [shape.categoryName, shape.score]) ?? []
    );
    const eyeBlinkConfidence = ((faceBlendshapes["eyeBlinkLeft"] ?? 0) + (faceBlendshapes["eyeBlinkRight"] ?? 0)) / 2;
    if (faceLandmarks.length > 0) {
      const drawPath = new Path2D();
      if (drawType !== "circle") {
        const box = { minX: imageWidth, minY: imageHeight, maxX: 0, maxY: 0 };
        faceLandmarks.forEach((point) => {
          const x = point.x * imageWidth;
          const y = point.y * imageHeight;
          box.minX = Math.min(box.minX, x);
          box.minY = Math.min(box.minY, y);
          box.maxX = Math.max(box.maxX, x);
          box.maxY = Math.max(box.maxY, y);
        });
        if (drawType === "corners") {
          const size = Math.round(Math.sqrt(Math.pow(box.maxX - box.minX, 2) + Math.pow(box.maxY - box.minY, 2)) * 0.1);
          drawPath.moveTo(box.minX, box.minY + size);
          drawPath.lineTo(box.minX, box.minY);
          drawPath.lineTo(box.minX + size, box.minY);
          drawPath.moveTo(box.maxX - size, box.minY);
          drawPath.lineTo(box.maxX, box.minY);
          drawPath.lineTo(box.maxX, box.minY + size);
          drawPath.moveTo(box.maxX, box.maxY - size);
          drawPath.lineTo(box.maxX, box.maxY);
          drawPath.lineTo(box.maxX - size, box.maxY);
          drawPath.moveTo(box.minX + size, box.maxY);
          drawPath.lineTo(box.minX, box.maxY);
          drawPath.lineTo(box.minX, box.maxY - size);
        } else drawPath.rect(box.minX, box.minY, box.maxX - box.minX, box.maxY - box.minY);
      } else
        drawPath.addPath(
          createPath(
            [
              10, 338, 297, 332, 284, 251, 389, 356, 454, 323, 361, 288, 397, 365, 379, 378, 400, 377, 152, 148, 176, 149, 150,
              136, 172, 58, 132, 93, 234, 127, 162, 21, 54, 103, 67, 109,
            ].map((idx) => [faceLandmarks[idx].x * imageWidth, faceLandmarks[idx].y * imageHeight])
          )
        );
      const box = { minX: imageWidth, minY: imageHeight, maxX: 0, maxY: 0 };
      const points = [
        114, 121, 120, 119, 118, 117, 111, 116, 123, 147, 187, 207, 206, 203, 142, 126, 217, 343, 350, 349, 348, 347, 346, 340,
        345, 352, 376, 411, 427, 426, 423, 371, 355, 437,
      ].map((idx) => {
        const x = faceLandmarks[idx].x * imageWidth;
        const y = faceLandmarks[idx].y * imageHeight;
        box.minX = Math.min(box.minX, x);
        box.minY = Math.min(box.minY, y);
        box.maxX = Math.max(box.maxX, x);
        box.maxY = Math.max(box.maxY, y);
        return [x, y];
      });
      const roiPath = new Path2D();
      roiPath.addPath(createPath(points.slice(0, 17)));
      roiPath.addPath(createPath(points.slice(17)));
      return {
        isFaceInView: true,
        eyeBlinkConfidence,
        drawPath,
        roiPath,
        roiBox: { x: box.minX, y: box.minY, w: box.maxX - box.minX, h: box.maxY - box.minY },
      };
    } else return { isFaceInView: false, eyeBlinkConfidence };
  };

  const calcRGB_fromImageData = (imgData) => {
    let count = 0,
      sumRGB = { r: 0, g: 0, b: 0 };
    for (let i = 0; i < imgData.data.length; i += 4) {
      if (imgData.data[i + 3] > 0) {
        count++;
        sumRGB.r += imgData.data[i];
        sumRGB.g += imgData.data[i + 1];
        sumRGB.b += imgData.data[i + 2];
      }
    }
    return { r: sumRGB.r / count, g: sumRGB.g / count, b: sumRGB.b / count };
  };

  const drawCanvas = ({ drawPath, roiPath, roiBox }) => {
    const imageWidth = video.videoWidth;
    const imageHeight = video.videoHeight;
    let avgRGB = { r: 0, g: 0, b: 0 };
    ctx.save();
    ctx.clearRect(0, 0, imageWidth, imageHeight);
    if (typeof roiPath !== "undefined") ctx.clip(roiPath);
    ctx.drawImage(video, 0, 0, imageWidth, imageHeight);
    if (typeof roiBox !== "undefined") avgRGB = calcRGB_fromImageData(ctx.getImageData(roiBox.x, roiBox.y, roiBox.w, roiBox.h));
    if (typeof roiPath !== "undefined") {
      ctx.restore();
      ctx.clearRect(0, 0, imageWidth, imageHeight);
      ctx.drawImage(video, 0, 0, imageWidth, imageHeight);
    }
    if (typeof drawPath !== "undefined") {
      ctx.lineWidth = 3;
      ctx.strokeStyle = drawColor;
      ctx.stroke(drawPath);
    }
    ctx.restore();
    return avgRGB;
  };

  const scan = (loop_start_time = 0) => {
    const timeElapsed = loop_start_time - start_time;
    try {
      if (timeElapsed <= calibrationTime) {
        drawCanvas({});
        detectionResult = getRegion(timeElapsed);
        calibrationFPS =
          (calibrationFPS * calibrationFrameCount + 1000 / (performance.now() - loop_start_time)) / ++calibrationFrameCount;
        if (detectionResult.eyeBlinkConfidence > 0.4) eyeClosedTime = timeElapsed;
        else eyeOpenedTime = timeElapsed;
        onFrameCallback({
          timeElapsed,
          type: "calibration",
          percentage: Math.round((timeElapsed / calibrationTime) * 100),
          isFaceInView: detectionResult.isFaceInView,
          isLiteMode: false,
        });
        frameID = requestAnimationFrame(scan);
      } else if (timeElapsed <= calibrationTime + scanTime) {
        const isLiteMode = calibrationFPS < (strictMode ? 10 : 15);
        if (isLiteMode) {
          detectionResult.isFaceInView = true;
          if (Math.round((timeElapsed - calibrationTime) / 1000) % liteModeInterval === 0)
            detectionResult = getRegion(timeElapsed);
        } else {
          detectionResult = getRegion(timeElapsed);
          if (detectionResult.eyeBlinkConfidence > 0.4) eyeClosedTime = timeElapsed;
          else eyeOpenedTime = timeElapsed;
          if (Math.abs(eyeClosedTime - eyeOpenedTime) / 1000 > (strictMode ? 20 : 30)) {
            stopScan(true);
            onErrorCallback(
              new Error(
                "No suitable subject detected. If the issue persists, consider adjusting the framing or removing any obstructions from the view."
              )
            );
            return;
          }
        }
        const { isFaceInView = true, eyeBlinkConfidence = 0, ...paths } = detectionResult;
        if (isFaceInView) noDetectionCount = 0;
        else {
          if (strictMode) noDetectionCount = 101;
          else noDetectionCount++;
        }
        if (noDetectionCount > 100) {
          stopScan(true);
          onErrorCallback(new Error("Face not detected."));
          return;
        }
        const intensity = drawCanvas(paths);
        raw_intensity.push(intensity);
        ppg_time.push(performance.now() - start_time);
        onFrameCallback({
          timeElapsed,
          type: "scan",
          percentage: Math.round(((timeElapsed - calibrationTime) / scanTime) * 100),
          isFaceInView,
          isLiteMode,
        });
        frameID = requestAnimationFrame(scan);
      } else stopScan();
    } catch (err) {
      stopScan(true);
      onErrorCallback(new Error("Sorry we're unable to compute the signal. Please try again.", { cause: err }));
    }
  };

  const startScan = async ({
    calibrationTime_inSec = 10,
    scanTime_inSec = 60,
    liteModeRedetectionInterval_inSec = 3,
    models_path = "https://content.careplix.com/careplix-web-algo-models",
    draw_type = "face-circle",
    draw_color = "#fff",
    strict_mode = false,
    videoElement,
    canvasElement,
  }) => {
    raw_intensity = [];
    ppg_time = [];
    isScanning = false;
    eyeClosedTime = eyeOpenedTime = -1;
    calibrationFrameCount = calibrationFPS = noDetectionCount = 0;
    detectionResult = {};

    try {
      if (typeof calibrationTime_inSec !== "number" || calibrationTime_inSec < 3 || calibrationTime_inSec > 20)
        throw new Error("Calibration duration can be between 3-20 seconds.");
      if (typeof scanTime_inSec !== "number" || scanTime_inSec < 30 || scanTime_inSec > 120 || scanTime_inSec % 5 !== 0)
        throw new Error("Scan duration can be between 30-120 seconds (multiple of 5).");
      if (calibrationTime_inSec > scanTime_inSec) throw new Error("Scan duration cannot be smaller than Calibration duration.");
      calibrationTime = calibrationTime_inSec * 1000;
      scanTime = scanTime_inSec * 1000;
      if (
        typeof liteModeRedetectionInterval_inSec !== "number" ||
        liteModeRedetectionInterval_inSec < 2 ||
        liteModeRedetectionInterval_inSec > 5
      )
        throw new Error("Lite mode Re-Detection Interval can be between 2-5 seconds.");
      liteModeInterval = Math.round(liteModeRedetectionInterval_inSec);
      switch (draw_type) {
        case "bounding-box":
          drawType = "box";
          break;
        case "corner-box":
          drawType = "corners";
          break;
        default:
          console.error("Provided Draw-Type is Invalid!\nSwitching to default...");
        case undefined:
        case "face-circle":
          drawType = "circle";
      }
      const s = new Option().style;
      s.color = draw_color;
      if (s.color.length === 0) {
        console.error("Provided Draw-Color is Invalid!\nSwitching to default...");
        drawColor = "#fff";
      } else drawColor = s.color;
      if (typeof strict_mode !== "boolean") {
        console.error("Provided Strict Mode value is Invalid!\nSwitching to default...");
        strictMode = false;
      } else strictMode = strict_mode;

      navigator.wakeLock
        ?.request("screen")
        .then((wakeLockSentinel) => {
          wakeLock = wakeLockSentinel;
          console.log("WakeLock Active.");
        })
        .catch((err) => {
          console.log("WakeLock Error.");
          console.error(err);
        });

      try {
        const vision = await FilesetResolver.forVisionTasks(models_path);
        faceLandmarkDetector = await FaceLandmarker.createFromOptions(vision, {
          baseOptions: { modelAssetPath: models_path + "/face_landmarker.task", delegate: "GPU" },
          runningMode: "VIDEO",
          numFaces: 1,
          minFaceDetectionConfidence: 0.9,
          minFacePresenceConfidence: 0.9,
          minTrackingConfidence: 0.9,
          outputFaceBlendshapes: true,
          outputFacialTransformationMatrixes: false,
        });
      } catch (err) {
        new Error("Please check your internet connection & try again.", { cause: err });
      }

      video = typeof videoElement === "undefined" ? document.getElementById("videoInput") : videoElement;
      if (video) {
        await setupCamera();
        await video.play?.();
      } else throw new Error("Cannot get the video element.");

      canvas = typeof canvasElement === "undefined" ? document.getElementById("canvasOutput") : canvasElement;
      if (canvas) {
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        ctx = canvas.getContext("2d");
      } else throw new Error("Cannot get the canvas element.");

      start_time = performance.now();
      frameID = requestAnimationFrame(scan);
      isScanning = true;
      window.onblur = () => {
        stopScan(true);
        onErrorCallback(
          new Error("App functionality disabled in the Background. Keep it in the Foreground for proper operation.")
        );
      };
    } catch (err) {
      stopScan(true);
      onErrorCallback(err);
      throw new Error("Facescan Initialization Error.", { cause: err });
    }
  };

  return {
    startScan,
    stopScan,
    onFrame: (callback = ({ timeElapsed = 0, type = "", percentage = 0, isFaceInView = false, isLiteMode = false }) => {}) => {
      if (typeof callback === "function") onFrameCallback = callback;
    },
    onScanFinish: (callback = ({ raw_intensity = [], ppg_time = [], average_fps = 0 }) => {}) => {
      if (typeof callback === "function") onScanFinishCallback = callback;
    },
    onError: (callback = (err = new Error("Facescan Error.")) => {}) => {
      if (typeof callback === "function") onErrorCallback = callback;
    },
    get isInitializing() {
      return () => !Boolean(isScanning);
    },
    get isScanning() {
      return () => Boolean(isScanning);
    },
  };
})();

export default faceScan;
